
.home_container{
    display: grid;
    grid-template-columns: 50% 50%;
    grid-gap: 5%;
    gap: 5%;
    place-items: center;
}
.background_image img{
    width: 100%;
    overflow: hidden;
}

/* media query */
@media screen and (max-width: 1024px) {
    .home_container{
        grid-template-columns: 1fr;
    }
  }
.carousel{
    width: 70%;
    border: 2px solid black;
}
.about_container{
    display: grid;
    grid-template-columns: 40% 50%;
    grid-gap: 5%;
    gap: 5%;
    margin-top: 5%;
}
.image img{
    width: 100%;
    overflow: hidden;
}
.accordion-button::after {
    -webkit-filter: invert(1);
            filter: invert(1);
}

@media screen and (max-width: 1020px) {
    .carousel{
        width: 100%;
    }
    .about_container{
        grid-template-columns: 1fr;
    }
}

@media screen and (max-width: 600px) {
    .carousel{
        width: 100%;
    }
}
.Contact_container{
    display: grid;
    grid-template-columns: 50% 50%;
    grid-gap: 5%;
    gap: 5%;
}

.form{
    margin-top: 5%;
}
.image img{
    width: 100%;
    overflow: hidden;
}

/* media query */
@media screen and (max-width: 1024px) {
    .Contact_container{
        grid-template-columns: 1fr;
    }
  }
